import React from 'react';

import { Bold, hasData } from './shared';
import ResearchProjects from './ResearchProjects';
import meiSrc from "../img/mei.jpg";

import "antd/dist/antd.css";

const newsData = require('../data/news.json');

const Body = () => (
    <div>
        <p>
            Hey there—nice to meet you! I'm Jenny. I'm an incoming Ph.D. student at <a href="https://www.cmu.edu/" target="_blank" rel="noreferrer">
            Carnegie Mellon University</a>. Currently, I'm a Predoctoral Young Investigator at the <a href="https://allenai.org/" target="_blank" 
            rel="noreferrer">Allen Institute of Artificial Intelligence</a>.
        </p>
        <p>
            <Bold>I research ways to make programming better for everyone</Bold> by studying developers, programming communities, and building 
            intelligent tools that make building software easier.
        </p>
        <p>
            My work has spanned across networking, machine learning, human-computer interaction, and software engineering, but nowadays my main 
            focus is on the latter three topics.
        </p>
        <p>
            (And, in case if you're wondering, my website logo is of my dog, <a href={meiSrc} target="_blank" rel="noreferrer">Mei 🐕</a>.)
        </p>
        {/* News section */}
        {hasData(newsData) && (
            <React.Fragment>
                <h2>News</h2>
                <ul>
                    {newsData
                        .map((e: {date: string, description: string}, i: number ) => {
                            return <li key={i}> <Bold>[{e.date}]</Bold> {e.description}</li>     
                        })
                    }
                </ul>
            </React.Fragment>
        )}

        <ResearchProjects/>
    </div>
);


export default Body;